h1, h2, h3, h4, h5, h6 {
  font-family: $base-font-family;
}

.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: $base-font-family;
}

h1, .h1 {
  font-weight: 300;
  font-size: 2em;
  margin-top: 0.67em;
  margin-bottom: 0.67em;
}

h2, .h2 {
  font-weight: 300;
  font-size: 1.5em;
  margin-top: 0.83em;
  margin-bottom: 0.83em;
}

h3, .h3 {
  font-weight: 400;
  font-size: 1.17em;
  margin-top: 1em;
  margin-bottom: 1em;
}

h4, .h4 {
  font-weight: 400;
  font-size: 1em;
  margin-top: 1.33em;
  margin-bottom: 1.33em;
}

h5, .h5 {
  font-weight: 500;
  font-size: 0.83em;
  margin-top: 1.67em;
  margin-bottom: 1.67em;
}

h6, .h6 {
  font-weight: 500;
  font-size: 0.67em;
  margin-top: 2.33em;
  margin-bottom: 2.33em;
}

b, strong {
  font-weight: 500;
}

p {
  display: block;
  margin-top:1em;
  margin-bottom: 1em;
  margin-left: 0;
  margin-right: 0;
}

