@charset "UTF-8";
/* You can add global styles to this file, and also import other style files */
@import "@angular/material/prebuilt-themes/deeppurple-amber.css";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

html {
  font-size: 16px;
  line-height: 1.3;
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

a, button {
  background: none;
  border: none;
  text-decoration: none;
  cursor: pointer;
  -webkit-text-decoration-skip: objects;
}

button::-moz-focus-inner {
  border: 0;
}

a:active,
a:hover,
button:active,
button:hover {
  outline-width: 0;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline dotted;
}

img {
  display: block;
  margin: 0;
  border: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

progress {
  vertical-align: baseline;
}

template,
[hidden] {
  display: none;
}

table, caption, tbody, tfoot, thead, tr, th, td {
  border: 0;
  font-size: 100%;
  vertical-align: middle;
  background: transparent;
  border-collapse: collapse;
}

table {
  width: 100%;
}

b,
strong {
  font-weight: bolder;
}

dfn {
  font-style: italic;
}

mark {
  background-color: #ff0;
  color: #000;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

svg:not(:root) {
  overflow: hidden;
}

code,
kbd,
pre,
samp {
  font-family: monospace;
  font-size: 1em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

input, textarea, select, button {
  font: inherit;
}

.app-a {
  color: #0277bd;
  transition: 0.25s;
  cursor: pointer;
  margin-right: 1em;
}
.app-a:hover {
  color: #004c8c;
}
.app-a_active {
  color: #004c8c;
}

.app-button {
  padding: 8px 20px;
  background: transparent;
  border-radius: 8px;
  border: 2px solid #0277bd;
  transition: 0.25s;
  margin: 0 4px;
}
.app-button:hover, .app-button:focus {
  background: #0277bd;
  border: 2px solid #0277bd;
  color: #ffffff;
}

code {
  white-space: pre;
}

.app-input {
  width: 100%;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.25);
  height: 36px;
  line-height: 18px;
  padding: 0 15px;
  outline: none;
}
.app-input:focus {
  border: 1px solid #004c8c;
}
.app-input:disabled {
  background: rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(0, 0, 0, 0.15);
  color: rgba(0, 0, 0, 0.38);
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Roboto", sans-serif;
}

.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: "Roboto", sans-serif;
}

h1, .h1 {
  font-weight: 300;
  font-size: 2em;
  margin-top: 0.67em;
  margin-bottom: 0.67em;
}

h2, .h2 {
  font-weight: 300;
  font-size: 1.5em;
  margin-top: 0.83em;
  margin-bottom: 0.83em;
}

h3, .h3 {
  font-weight: 400;
  font-size: 1.17em;
  margin-top: 1em;
  margin-bottom: 1em;
}

h4, .h4 {
  font-weight: 400;
  font-size: 1em;
  margin-top: 1.33em;
  margin-bottom: 1.33em;
}

h5, .h5 {
  font-weight: 500;
  font-size: 0.83em;
  margin-top: 1.67em;
  margin-bottom: 1.67em;
}

h6, .h6 {
  font-weight: 500;
  font-size: 0.67em;
  margin-top: 2.33em;
  margin-bottom: 2.33em;
}

b, strong {
  font-weight: 500;
}

p {
  display: block;
  margin-top: 1em;
  margin-bottom: 1em;
  margin-left: 0;
  margin-right: 0;
}

.app-page {
  max-width: 900px;
  margin: 16px auto;
  background: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 32px;
  position: relative;
}
.app-page__header {
  margin-bottom: 32px;
  text-align: center;
}
.app-page__footer {
  margin-top: 32px;
  display: flex;
  justify-content: flex-end;
}

